<template>
	<view class="proj-item">
		<view :class="isTime.now?'active':''" class="time">{{info.publish_time+'上线'}}</view>
		<view :class="[!info.status?'verify':'',border?'border':'']" class="item-cen">
			<view class="user">
				<view class="user-info">
					<view class="face-box">
						<image class="face" :src="info.head_img||logo"></image>
						<!-- <image ></image> -->
					</view>{{''+(info.nickname||'小松鼠')+'   爆料'}}
				</view>
				<image class="lately" src="../../static/img/proj/lately.png" v-if="!info.published&&info.status ">
				</image>
			</view>
			<view class="info">
				<view class="name">{{info.project_name}}</view>
			</view>
		</view>
	</view>

</template>

<script>
	export default {
		props: {
			info: Object,
			isTime: Object,
			border: Boolean
		},
		data() {
			return {
				logo: "../../static/img/logo_.png",
			}
		},
		methods: {
			toLink: function(e) {
				this.info.status && n.navigateTo({
					url: e
				});
			},
			declareFn: function(n, e, o) {
				this.$emit("declare", n, e, o, this.index);
			}
		}
	}
</script>

<style lang="scss">
	.proj-item {
		width: 100%;
		padding-left: 50rpx;
		padding-bottom: 30rpx;
		box-sizing: border-box;
	}

	.proj-item .item-cen {
		background: #fff;
		box-shadow: 0rpx 8rpx 40rpx -7rpx rgba(112, 144, 176, .2);
		border-radius: 10px;
		padding: 34rpx 24rpx 38rpx;
		position: relative;
	}

	.proj-item .item-cen .user {
		-webkit-box-pack: justify;
		justify-content: space-between;
		padding: 0rpx 0 26rpx;
	}

	.proj-item .item-cen .user,
	.proj-item .item-cen .user .user-info {
		display: flex;
		-webkit-box-align: center;
		align-items: center;
	}

	.proj-item .item-cen .user .user-info {
		font-size: 32rpx;
		color: #888;
	}

	.proj-item .item-cen .user .user-info .face-box {
		display: inline-block;
		border-radius: 50%;
		width: 33rpx;
		height: 33rpx;
		overflow: hidden;
		position: relative;
		box-sizing: border-box;
		margin-right: 18rpx;
	}

	.proj-item .item-cen .user .user-info .face-box .face {
		width: 100%;
		height: 100%;
		display: block;
		border-radius: 50%;
		margin-right: 18rpx;
	}

	.proj-item .item-cen .user .lately {
		width: 138rpx;
		height: 36rpx;
		display: block;
	}

	.proj-item .item-cen .info {
		display: flex;
		-webkit-box-pack: justify;
		justify-content: space-between;
		-webkit-box-align: center;
		align-items: center;
	}

	.proj-item .item-cen .info .name {
		font-size: 36rpx;
		color: #fd2922;
		font-weight: 700;
	}

	.proj-item .item-cen .info .look {
		font-size: 32rpx;
		color: #353535;
	}

	.proj-item .item-cen::before {
		content: "";
		width: 1px;
		height: calc(100% + 80rpx);
		position: absolute;
		top: -22rpx;
		left: -40rpx;
		background: #b2b2b2;
	}

	.proj-item .item-cen.border::before {
		width: 0;
	}

	.proj-item .item-cen.verify::after {
		content: "";
		width: 80rpx;
		height: 83rpx;
		display: block;
		position: absolute;
		top: 0;
		right: 0rpx;
		background: url(data:image/png;base64,) no-repeat 0 100%;
		background-size: cover;
		z-index: 1;
	}

	.proj-item .time {
		font-size: 28rpx;
		color: #353535;
		position: relative;
		padding: 10rpx 0;
	}

	.proj-item .time::before {
		content: "";
		width: 16rpx;
		height: 16rpx;
		left: -47rpx;
		top: 50%;
		transform: translateY(-50%);
		display: block;
		border-radius: 50%;
		background: #b2b2b2;
		position: absolute;
		z-index: 2;
	}

	.proj-item .time.active::before {
		width: 22rpx;
		height: 22rpx;
		left: -50rpx;
		background: #307cf8;
		z-index: 2;
	}
</style>
